import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
@Injectable({
  providedIn: 'root'
})
export class DataService {
  

  constructor(
    private http: HttpClient,
    
  ) {
      

   }
// getting url from environment 
   private getbaseurl() {
    return environment.Base_url;
  }

// post method 
  post(url: string, body: any) {
    return this.http
      .post(url, body)
      .toPromise()
      .catch(this.handleError);
  }

  // get method 
  get(url: string) {
    return this.http
      .get(url)
      .toPromise()
      .catch(this.handleError);
  }


  // error handling
  private handleError(error: Response) {
    console.error(error);
    return {errorCode: 1, errorMsg: 'Server error'};//throw(error.json(). || );
  }

  // api call for getting planets list 
  getplanets() {
    return this.get(this.getbaseurl() + 'planets')
      .then((response: any) => {
        if (response)
          return response;
      });
  }
 
  // api call for getting vehicles list 
  getvehicles() {
    return this.get(this.getbaseurl() + 'vehicles')
      .then((response: any) => {
        if (response)
          return response;
      });
  }

  Gettoken()
  {
    return this.post(this.getbaseurl() + 'token','')
    .then((response: any) => {
      if (response)
        return response;
    });
  }

  FindFalcone(body:any)
  {
    return this.post(this.getbaseurl() + 'find',body)
    .then((response: any) => {
      if (response)
        return response;
    });
  }
}
 